import React, { useEffect,useState } from 'react'
import { Form, Modal, Input, Row, Col, Button, DatePicker, message,Select } from 'antd'
import { ListSelect, SelectPeople } from '../../../static/own-comp'
import { setEditSubHospital } from '../../../function/set'
import { useMutation, useQuery } from '@apollo/client'
import { UPDATE_PERSON_BY_HOSPITALINFO } from '../../../graphql/hospital'
import moment from 'moment'
import { GET_ALL_PERSONINFO_FOR_SAMPLE_ADD } from '../../../graphql/people'
import { convertPersonalToSelect } from '../../../function/fn'
import AddPeople from '../../people/modal/addPeople'

const {Option} = Select
export default function EditSubHospital({ open, setOpen, data, setRefetch,hospitalId }) {
    let [form] = Form.useForm()
    const [personalInfoId, setPersonalInfoId] = useState("");

    const [openPersonal, setOpenPersonal] = useState(false);
    const [personalInfoData, setPersonalInfoData] = useState({});

    const [page,setPage] = useState(1)
    const [limit,setLimit] = useState(200)
    const [keyword, setKeyword] = useState("")
    const [personalAllData,setPersonalAllData] = useState([])

    const {data:personalInfo} = useQuery(GET_ALL_PERSONINFO_FOR_SAMPLE_ADD,{
        variables:{
        page:page,
        limit:limit,
        keyword:keyword,
        startDate:null,
        endDate:null,
        currentState:null
        },
        onCompleted:({getPersonalInfoWithPagination})=>{
        // console.log(getPersonalInfoWithPagination)
        setPersonalAllData(getPersonalInfoWithPagination?.personalInfos)
        }
    })
  
    useEffect(()=>{
        if(personalInfo){
        setPersonalAllData(personalInfo?.getPersonalInfoWithPagination?.personalInfos)
        console.log(data)
        }
    },[keyword,personalInfo])

    const [updatePatientFromHospital, { loading }] = useMutation(UPDATE_PERSON_BY_HOSPITALINFO, {
        onCompleted: ({updatePatientFromHospital}) => {
            if(updatePatientFromHospital.success===true){
                setRefetch()
                message.success("")
                // message.error(updatePatientFromHospital.message)
            }else{
                message.error(updatePatientFromHospital.message)
            }
            
        },
        onError:(error)=>{
            message.error(error.message)
        }
    })

    console.log(data)

    useEffect(() => {
        if(data!==undefined){
            form.setFieldsValue({
                ...data,
                date_in: data?.date_in ===null || data?.date_in ===undefined ? null : moment(data?.date_in),
                date_out: data?.date_out ===null || data?.date_out ===undefined ? null : moment(data?.date_out),
            })
        }
        
    }, [data])

    const onFinish = (values) => {
        console.log('Success:', values);
        updatePatientFromHospital({
            variables: {
                ...values,
                date_in: moment(values.date_in).format(),
                date_out: values.date_out === undefined || values.date_out === null ? null : moment(values.date_out).format(),
                personalInfoId: data.personalInfoId,
                hospitalId: data.id,
                hospitalInfo:hospitalId,
                lat:data.lat,
                long:data.long
            }
        })

        setOpen(false)
        form.resetFields()
    };

    const onFinishFailed = (errorInfo) => {
        console.log('Failed:', errorInfo);
    };

    const setToPeopleFn = (e) => {
        form.setFieldsValue({
            personalInfo: e
        });
    };

        //handle select people
    const handleSearchPeople = (e) => {
        if (e) {
        setKeyword(e)
        } else {
        // setPData([])
        setPersonalAllData([])
        }
    };

    const setToPersonalInfoFn = (e) => {
        form.setFieldsValue({
          personalInfoId: e,
        });
        if (e === "new") {
          setOpenPersonal(true);
        }
    
        setPersonalInfoId(e);
      };

      const callbackPersonalInfo = (e) => {

        if (e === "") {
    
          // setOpen(false)
          setPersonalInfoId(e);
          form.setFieldsValue({
            personalInfoId: null,
          });
        } else {
          setPersonalInfoData(e);
    
          form.setFieldsValue({
            personalInfoName: e.lastName + " " + e.firstName,
          });
        }
      };

    return (
        <Modal
            title=""
            visible={open}
            onOk={() => setOpen(false)}
            onCancel={() => setOpen(false)}
            footer={null}
        >
            <AddPeople
                open={openPersonal}
                setOpen={setOpenPersonal}
                personalInfoId={personalInfoId}
                setPersonalInfoId={callbackPersonalInfo}
                personalInfoData={personalAllData}
            />
            <Form
                form={form}
                name="editSubHospital"
                onFinish={onFinish}
                onFinishFailed={onFinishFailed}
            >
                <Row>
                    <Col xs={24} md={{ span: 11 }}>
                        <Form.Item
                            name="personalInfoId"
                            rules={[{ required: true, message: "!" }]}
                            >
                            <SelectPeople disabled={true} data={convertPersonalToSelect(personalAllData)} title="" handleSearchPeople={handleSearchPeople} setValue={setToPersonalInfoFn} />
            
                            {/* <SelectPeople disabled={true} data={convertPersonalToSelect(personalAllData)} title="" handleSearchPeople={handleSearchPeople} setValue={setToPersonalInfoFn} /> */}
                        </Form.Item>
                    </Col>

                    {/* <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="personalType"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col> */}

                    {/* <Col xs={24} md={{ span: 11,offset:2}}>
                        <Form.Item
                            name="in"
                            rules={[{ required: true, message: '!' }]}
                        >

                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col> */}

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="covidVariant"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value="COVID-19">COVID-19</Option>
                                <Option value="DELTA">DELTA</Option>
                                <Option value="ALPHA">ALPHA</Option>
                                <Option value=""></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 0 }}>
                        <Form.Item
                            name="date_in"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 11, offset: 2 }}>
                        <Form.Item
                            name="date_out"
                        // rules={[{ required: true, message: '!' }]}
                        >
                            <DatePicker placeholder="" style={{ width: "100%" }} />
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24, offset: 0 }}>
                        <Form.Item
                            name="coorporate"
                            rules={[{ required: true, message: '!' }]}
                        >
                            <Select placeholder="" style={{ width: "100%" }}>
                                <Option value={true}></Option>
                                <Option value={false}></Option>
                            </Select>
                        </Form.Item>
                    </Col>

                    <Col xs={24} md={{ span: 24 }}>
                        <Form.Item
                            name="description"
                        //rules={[{ required: true, message: '!' }]}
                        >

                            <Input placeholder="" />
                        </Form.Item>
                    </Col>

                    <Col xs={24}>
                        <Button
                            htmlType="submit"
                            type="primary"
                            style={{ width: "100%" }}
                        >
                            
                        </Button>
                    </Col>
                </Row>
            </Form>
        </Modal>
    )
}
